.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media (max-width: 1920px){.page{display:flex;width:100%}}@media (max-width: 959px){.page{display:block}}.header-bar.app-bar{flex-grow:1;background:white !important;height:4em !important}.header-bar .tool-bar{display:-ms-inline-flexbox;justify-content:space-between}.header-bar .logo{height:1.5rem}.header-bar .person-icon{color:#212121;height:1.3rem}.header-bar .invite-icon{color:#212121;height:1.3rem}#invitation-menu .MuiMenu-list{max-width:50em;min-width:30em}#invitation-menu .accepted{color:darkgreen !important;margin-left:auto;padding-right:.5em;padding-left:3em}#invitation-menu .declined{color:darkred !important;margin-left:auto;padding-right:.5em;padding-left:3em}#invitation-menu .need-response{background-color:lightgrey !important;float:right !important;margin-left:auto !important;padding-right:.5em !important}.main-not-found{background:linear-gradient(180deg, #6868D5 40%, #9cf 90%);height:100vh;justify-content:center}.main-not-found .paper{margin-left:10%;margin-right:10%;justify-self:center;height:80vh;justify-content:center}.main-not-found .title{padding-top:1%;font-family:"Montserrat" !important;font-weight:700 !important;font-size:1.2rem !important;line-height:!important;letter-spacing:!important;font-size:3.5rem !important;text-align:center}.main-not-found .subtitle{padding-top:1%;font-family:"Montserrat" !important;font-weight:200 !important;font-size:1.1rem !important;line-height:!important;letter-spacing:!important;font-size:2.5rem !important;text-align:center}.main-not-found .button-box{padding-top:5%;display:flex;justify-content:center}.main-not-found .button{background-color:#6868D5;width:20%;border-radius:15px !important}.main-not-found .map-box{padding-top:10%;display:flex;justify-content:center}.main-not-found .map-icon{font-size:5rem}

.temp-page-container.text-div{text-align:center;margin:auto;width:50%;padding:5%}.temp-page-container .title{margin-bottom:10%}.temp-page-container .text{margin:5%}.temp-page-container .text.link{font-size:2rem}.temp-page-container .thanks{margin-top:10%}



